import { memo, useCallback, useEffect, useRef, useState } from 'react'
import { useForm } from 'react-hook-form'
import { useDispatch } from 'react-redux'
import { AddIcon } from '@chakra-ui/icons'
import {
  Box,
  Heading,
  Input,
  Stack,
  Text,
  useDisclosure,
  Collapse,
} from '@chakra-ui/react'

import FormButton from '../UI/FormButton/FormButton'
import { onAddChat } from '../../store/chatSlice'

import styles from './AddItemChat.module.scss'
import AddImageChat from '../AddImageChat/AddImageChat'

const AddItemChat = () => {
  const inputTitleRef = useRef(null)

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [image, setImage] = useState()
  const fileReader = new FileReader()
  const [imageURL, setImageURL] = useState()

  const { isOpen, onToggle, onClose } = useDisclosure()

  const dispatch = useDispatch()

  useEffect(() => {
    inputTitleRef.current.focus()
  }, [isOpen])

  const addChat = () => {
    if (!title) {
      return
    }

    dispatch(onAddChat({ title, description, imageURL }))
    setTitle('')
    setDescription('')
    reset()
    onClose()
  }

  const addChatEnter = e => {
    if (!title) {
      return
    }

    if (e.key === 'Enter') {
      dispatch(onAddChat({ title, description }))
      setTitle('')
      setDescription('')
      reset()
      onClose()
    }
  }

  const handleTitleChange = useCallback(event => {
    setTitle(event.target.value)
  }, [])

  const handleDescriptionChange = useCallback(event => {
    setDescription(event.target.value)
  }, [])

  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({
    mode: 'onBlur',
  })

  const onSubmit = data => {
    console.log(JSON.stringify(data))
    reset()
  }

  fileReader.onloadend = () => {
    setImageURL(fileReader.result)
  }
  const handleImageChange = e => {
    e.preventDefault()
    const files = e.target.files
    if (files && files.length > 0) {
      setImage(files)
      fileReader.readAsDataURL(files[0])
    }
  }

  return (
    <>
      <Collapse in={isOpen} animateOpacity>
        <Stack position={'relative'}>
          <Box className={styles['container-form']} rounded='md' shadow='md'>
            <Heading className={styles.header}> </Heading>

            <Box className={styles.body}>
              <AddImageChat
                imageURL={imageURL}
                file={image}
                onChange={handleImageChange}
              />
              <form onSubmit={handleSubmit(onSubmit)}>
                <Input
                  {...register('chat-name', {
                    required: '   !',
                  })}
                  value={title}
                  onChange={handleTitleChange}
                  onKeyUp={addChatEnter}
                  variant={'flushed'}
                  placeholder={' '}
                  autoComplete='off'
                  ref={inputTitleRef}
                />

                <Text h={'20px'}>
                  {errors?.chatname && (
                    <p style={{ color: 'red' }}>
                      {errors?.chatname?.message ||
                        '     !'}
                    </p>
                  )}
                </Text>

                <Input
                  {...register('description')}
                  variant={'flushed'}
                  value={description}
                  onKeyUp={addChatEnter}
                  onChange={handleDescriptionChange}
                  placeholder={' ()'}
                  autoComplete='off'
                />
              </form>
            </Box>

            <Box className={styles.footer}>
              <FormButton
                type={'submit'}
                onClick={addChat}
                colorScheme='blue'
                mr={3}
              >
                
              </FormButton>
            </Box>
          </Box>
        </Stack>
      </Collapse>

      <div className={styles.container}>
        <FormButton className={styles['button-add']} onClick={onToggle}>
          <AddIcon className={styles.icon} />
        </FormButton>
      </div>
    </>
  )
}

export default memo(AddItemChat)
