import { useState } from 'react'
import { useForm } from 'react-hook-form'
import {
  CardBody,
  Text,
  Card,
  CardHeader,
  Stack,
  Heading,
  Input,
  InputGroup,
  InputRightElement,
  Divider,
  HStack,
} from '@chakra-ui/react'

import { authUrl } from '../../constants/urls'
import CustomLink from '../../components/CustomLink/CustomLink'
import FormButton from '../../components/UI/FormButton/FormButton'
import InfoPopover from '../../components/UI/InfoPopover/InfoPopover'

import styles from './RegContent.module.scss'

const RegContent = () => {
  const [showPas, setPasShow] = useState(false)
  const [showRepPas, setRepPasShow] = useState(false)

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: 'onBlur',
  })

  const onSubmit = data => {
    console.log(JSON.stringify(data))
  }

  const handleClickPassword = () => {
    setPasShow(!showPas)
  }
  const handleClickRepPassword = () => {
    setRepPasShow(!showRepPas)
  }

  return (
    <section className={styles.container}>
      <Card className={styles['card-container']}>
        <CardHeader>
          <Heading>
            <HStack className={styles['title-wrapper']}>
              <Text className={styles['title']}></Text>

              <InfoPopover
                children1={''}
                children2={
                  '       ,      '
                }
              />
            </HStack>
          </Heading>
        </CardHeader>

        <Divider />

        <CardBody>
          <form onSubmit={handleSubmit(onSubmit)}>
            <Stack mt='3' spacing='3'>
              <Input
                {...register('fullorgname', {
                  required: '  !',
                })}
                variant={'flushed'}
                placeholder={'  '}
                autoComplete='off'
              />

              <Text h={'15px'}>
                {errors?.fullorgname && (
                  <p style={{ color: 'red' }}>
                    {errors?.fullorgname?.message ||
                      '     !'}
                  </p>
                )}
              </Text>

              <Input
                {...register('shortname', {
                  required: '  !',
                })}
                variant={'flushed'}
                placeholder={'  '}
                autoComplete='off'
              />

              <Text h={'15px'}>
                {errors?.shortname && (
                  <p style={{ color: 'red' }}>
                    {errors?.shortname?.message ||
                      '     !'}
                  </p>
                )}
              </Text>

              <Input
                {...register('email', {
                  required: '  !',
                })}
                variant={'flushed'}
                placeholder={'Email'}
                autoComplete='off'
              />

              <Text h={'15px'}>
                {errors?.email && (
                  <p style={{ color: 'red' }}>
                    {errors?.email?.message || '    email!'}
                  </p>
                )}
              </Text>

              <InputGroup size='md'>
                <Input
                  {...register('newpass', {
                    required: '  !',
                    minLength: {
                      value: 5,
                      message: ' 5 !',
                    },
                  })}
                  pr='4.5rem'
                  variant={'flushed'}
                  type={showPas ? 'text' : 'password'}
                  placeholder={'  '}
                  autoComplete='off'
                />
                <InputRightElement width='4.5rem'>
                  <FormButton
                    h='1.75rem'
                    size='sm'
                    onClick={handleClickPassword}
                  >
                    {showPas ? '' : ''}
                  </FormButton>
                </InputRightElement>
              </InputGroup>

              <Text h={'15px'}>
                {errors?.newpass && (
                  <p style={{ color: 'red' }}>
                    {errors?.newpass?.message ||
                      '    !'}
                  </p>
                )}
              </Text>

              <InputGroup size='md'>
                <Input
                  {...register('repass', {
                    required: '  !',
                  })}
                  pr='4.5rem'
                  variant={'flushed'}
                  type={showRepPas ? 'text' : 'password'}
                  placeholder={' '}
                  autoComplete='off'
                />
                <InputRightElement width='4.5rem'>
                  <FormButton
                    h='1.75rem'
                    size='sm'
                    onClick={handleClickRepPassword}
                  >
                    {showRepPas ? '' : ''}
                  </FormButton>
                </InputRightElement>
              </InputGroup>

              <Text h={'15px'}>
                {errors?.reppass && (
                  <p style={{ color: 'red' }}>
                    {errors?.reppass?.message ||
                      '    !'}
                  </p>
                )}
              </Text>
            </Stack>

            <Divider mt={'4px'} />

            <HStack mt={'10px'}>
              <FormButton type={'submit'}></FormButton>
              <Divider orientation={'vertical'} />
              <Stack>
                <Text>
                  <span>
                    <CustomLink to={authUrl}></CustomLink>
                  </span>
                </Text>
              </Stack>
            </HStack>
          </form>
        </CardBody>
      </Card>
    </section>
  )
}

export default RegContent
